import React, {Component} from 'react';
import PropTypes from "prop-types";
import context from "../context/ContentContext";
import Parts from "./Parts";
import Fields from "./Fields";
import PropertyEdit from "../PropertyEdit";
import StatusEdit from "../StatusEdit";
import {Button, DialogActions, DialogContent, DialogTitle} from "@material-ui/core";

class PropertyForm extends Component {
    static contextType = context;

    state = {
        item: {
            _id: "",
            property: [],
            status: [],
        },
    };

    componentDidMount() {
        const {edit, getItem} = this.props;

        if (edit) {
            getItem(edit)
                .then(item => this.setState({item}));
        }
    }

    handleEdit = fieldName => {
        return fieldValue => {
            this.setState(({item}) => ({
                item: {
                    ...item,
                    [fieldName]: fieldValue,
                }
            }));
        }
    }

    handleSave = () => {
        const {edit, onClose, postItem, putItem} = this.props;

        if (edit) {
            putItem(edit, this.state.item)
                .then(() => onClose());
        } else {
            postItem(this.state.item)
                .then(() => onClose());
        }
    }

    render() {
        const
            {edit} = this.props,
            {item: {_id, property, status}} = this.state;

        return (
            <>
                <DialogTitle>
                    {this.renderTitle}
                </DialogTitle>

                <DialogContent>
                    <Parts
                        tabs={[
                            "Поля",
                            "Свойства",
                            "Статусы",
                        ]}
                    >
                        <Fields
                            _id={_id}
                            edit={edit}
                            onEdit={this.handleEdit}
                        />

                        <PropertyEdit
                            onEdit={this.handleEdit("property")}
                            values={property}
                        />

                        <StatusEdit
                            onEdit={this.handleEdit("status")}
                            values={status}
                        />
                    </Parts>
                </DialogContent>

                <DialogActions>
                    <Button onClick={this.handleClose}>
                        Cancel
                    </Button>

                    <Button onClick={this.handleSave}>
                        Save
                    </Button>
                </DialogActions>
            </>
        );
    }
}

PropertyForm.propTypes = {
    onClose: PropTypes.func,
    edit: PropTypes.string,
    getItem: PropTypes.func,
};

export {PropertyForm};

export default function (props) {
    const {property} = React.useContext(context);

    return (
        <PropertyForm
            {...props}
            {...property}
        />
    );
}
