import React from 'react';
import TableCell from "@material-ui/core/TableCell";

export default function Cell({renderCell, rowNumber, columns}) {
    const cellArray = [];

    for (let columnNumber = 0; columnNumber < columns; columnNumber++) {
        cellArray.push(
            <TableCell key={columnNumber}>
                {renderCell(columnNumber, rowNumber)}
            </TableCell>
        );
    }

    return cellArray;
}
