import React, {Component} from 'react';
import {Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField} from "@material-ui/core";
import {context} from "../ContentList/context";

class Form extends Component {
    static contextType = context;

    constructor(props) {
        super(props);

        this.state = {
            _id: "",
        };
    }

    componentDidMount() {
        const
            {status: {fetchStatusItem}} = this.context,
            {edit} = this.props;

        if (edit) {
            fetchStatusItem(edit)
                .then(item => this.setState({
                    _id: item._id,
                }));
        }
    }

    handleEdit(field) {
        return event => {
            const {target: {value}} = event;
            this.setState({[field]: value});
        }
    }

    get renderSlug() {
        const {_id} = this.state;

        return (
            <TextField
                autoFocus
                margin="dense"
                id="name"
                label="Status slug"
                type="text"
                fullWidth
                variant="standard"
                value={_id}
                onChange={this.handleEdit("_id")}
            />
        );
    }

    handleSave = item => {
        const
            {status: {postStatus, putStatus}} = this.context,
            {edit, onClose} = this.props;

        if (edit) {
            putStatus(this.state)
                .then(() => {
                    onClose()
                })
        } else {
            postStatus(this.state)
                .then(() => {
                    onClose()
                })
        }
    }

    render() {
        const {onClose} = this.props;

        return (
            <>
                <DialogTitle>
                    New Status
                </DialogTitle>

                <DialogContent>
                    {/*<DialogContentText>*/}
                    {/*    To subscribe to this website, please enter your email address here. We*/}
                    {/*    will send updates occasionally.*/}
                    {/*</DialogContentText>*/}

                    {this.renderSlug}
                </DialogContent>

                <DialogActions>
                    <Button onClick={onClose}>
                        Cancel
                    </Button>

                    <Button onClick={this.handleSave}>
                        Save
                    </Button>
                </DialogActions>
            </>
        );
    }
}

export default Form;
