import React from 'react';
import DocumentTable from "../../components/DocumentTable";
import mockData from "../../components/DocumentTable/mockData"


const {renderTitle, renderHeaderCell, renderCell, panel} = mockData

class Products extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            rowsPerPage: 10,
            rowsTotal: 28,
            page:0,
            rows:10,
        };

        this.handleChangePage = this.handleChangePage.bind(this);
    }

    handleChangePage(event, newPage){
        const {rows, rowsTotal} = this.state;
        let newRows

        if(rows * (newPage + 1) > rowsTotal){
            newRows = rowsTotal % rows;
        } else{
            newRows = 10;
        }

        this.setState({
            page:newPage,
            rows:newRows
        });
    }

    render() {
        const {page, rowsPerPage, rows, rowsTotal} = this.state;

        return(
            <DocumentTable
                renderTitle={renderTitle}
                renderCell={renderCell}
                renderHeaderCell={renderHeaderCell}
                columns={4}
                rows={rows}
                rowsTotal={rowsTotal}
                panel={panel}
                page={page}
                rowsPerPage={rowsPerPage}
                onChangePage={this.handleChangePage}
            />
        );
    }
}

Products.propTypes = {};

export default Products;
