import React from "react";
// import firebase from "firebase";
import {getStatusList, getStatusItem, postStatus, putStatus} from "./content/status";
import {getPropertyList, getPropertyItem, postProperty, putProperty} from "./content/property";

export const contentContext = React.createContext({});

export class ContentProvider extends React.Component {

    state = {
        product: [],
        section: [],
    };

    initFirebase() {
        // var firebaseConfig = {
        //     // apiKey: '<your-api-key>',
        //     // authDomain: '<your-auth-domain>',
        //     // databaseURL: '<your-database-url>',
        //     apiKey: "AIzaSyCeVSAFROSqq3_K26E4a6G_U5plMjBuICg",
        //     authDomain: "flutter966-6438f.firebaseapp.com",
        //     projectId: "flutter966-6438f",
        //     // storageBucket: "flutter966-6438f.appspot.com",
        //     messagingSenderId: "633269874431",
        //     appId: "1:633269874431:web:7a059db6fa7fa2067c66ca",
        //     measurementId: "G-867KGFNNET",
        //     storageBucket: 'gs://flutter966-6438f.appspot.com'
        // };
        // firebase.initializeApp(firebaseConfig);
        // // firebase.analytics();
        //
        // var storage = firebase.storage();
        //
        // return storage;
    }

    uploadFile(filename, base64 = "") {
        const store = this.initFirebase();
        const ref = store.ref();
        var mountainsRef = ref.child(filename);

        return mountainsRef.putString(base64, 'data_url')
            .then((snapshot) => {
                // https://firebasestorage.googleapis.com/v0/b/flutter966-6438f.appspot.com/o/applejack.jpg?alt=media

                console.log(snapshot);


                return "https://firebasestorage.googleapis.com/v0/b/"
                    + snapshot._delegate.metadata.bucket
                    + "/o/"
                    + snapshot._delegate.metadata.fullPath
                    + "?alt=media";
            });
    }

    fetchContent(entity, filter) {
        return fetch(
            `http://localhost:3003/${entity}/?${filter ?? ""}`,
        ).then(res => res.json())
    }

    putProductItem(product) {
        return fetch(
            `http://3.67.37.0/content/${product.id}/`,
            {
                method: "PUT",
                body: JSON.stringify(product),
                headers: {
                    'Content-Type': "application/json",
                }
            }
        ).then(res => res.json())
    }

    fetchProductList() {
        return this.fetchContent("content")
    }

    fetchProductItem(id) {
        return this.fetchContent("content", `filter=field_id-in-${id}`);
    }

    fetchPropertyList() {
        return this.fetchContent("property");
    }

    fetchStatusList() {
        return this.fetchContent("status");
    }

    fetchDescriptionList() {
        return this.fetchContent("description");
    }

    fetchSection() {
        return this.fetchContent("section")
            .then(result => {
                return result.map(item => {
                    return {
                        id: item._id,
                        ...item,
                    };
                })
            });
    }

    postSection() {
        return fetch(
            `http://localhost:3003/section/`,
            {
                method: "POST",
                body: JSON.stringify({
                    slug: "SOME SECTION"
                }),
                headers: {
                    'Content-Type': 'application/json',
                }
            }
        ).then(res => res.json())
    }

    render() {
        const
            {Provider} = contentContext,
            {children} = this.props;

        return (
            <Provider value={{
                fetchProductList: this.fetchProductList.bind(this),
                fetchProductItem: this.fetchProductItem.bind(this),
                fetchPropertyList: this.fetchPropertyList.bind(this),
                fetchDescriptionList: this.fetchDescriptionList.bind(this),
                putProductItem: this.putProductItem.bind(this),
                uploadFile: this.uploadFile.bind(this),
                section: {
                    fetchSection: this.fetchSection.bind(this),
                    postSection: this.postSection.bind(this),
                },
                status: {
                    getStatusList: getStatusList.bind(this),
                    getStatusItem: getStatusItem.bind(this),
                    postStatus: postStatus.bind(this),
                    putStatus: putStatus.bind(this),
                },
                property: {
                    getPropertyList: getPropertyList.bind(this),
                    getPropertyItem: getPropertyItem.bind(this),
                    postProperty: postProperty.bind(this),
                    putProperty: putProperty.bind(this),
                },
            }}>
                {children}
            </Provider>
        );
    }
}
