import React, {Component} from 'react';
import context from "../context/ContentContext";
import {Button, DialogActions, DialogContent, DialogTitle} from "@material-ui/core";
import Parts from "./Parts";
import Fields from "./Fields";

class StatusForm extends Component {
    static contextType = context;

    state = {
        item: {
            _id: "",
        },
    };

    componentDidMount() {
        const {edit, getItem} = this.props;

        if (edit) {
            getItem(edit)
                .then(item => this.setState({item}));
        }
    }

    handleEdit = field => {
        return event => {
            const {target: {value}} = event;

            this.setState(({item}) => ({
                item: {
                    ...item,
                    [field]: value,
                }
            }));
        }
    }

    handleSave = () => {
        const {edit, onClose, postItem, putItem} = this.props;

        if (edit) {
            putItem(edit, this.state.item)
                .then(() => onClose());
        } else {
            postItem(this.state.item)
                .then(() => onClose());
        }
    }

    get renderTitle() {
        const {edit} = this.props;

        if (edit) {
            return `Edit status: ${edit}`;
        } else {
            return "New status";
        }
    }

    render() {
        const
            {edit, onClose} = this.props,
            {item: {_id}} = this.state;

        return (
            <>
                <DialogTitle>
                    {this.renderTitle}
                </DialogTitle>

                <DialogContent>
                    <Parts>
                        <Fields
                            _id={_id}
                            edit={edit}
                            onEdit={this.handleEdit}
                        />
                    </Parts>
                </DialogContent>

                <DialogActions>
                    <Button onClick={onClose}>
                        Cancel
                    </Button>

                    <Button onClick={this.handleSave}>
                        Save
                    </Button>
                </DialogActions>
            </>
        );
    }
}

export default function (props) {
    const {status} = React.useContext(context);

    return (
        <StatusForm
            {...props}
            {...status}
        />
    );
}
