import React from 'react';
import {withStyles} from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import {Tab, Tabs, TextField} from "@material-ui/core";
import SwipeableViews from "react-swipeable-views";
import Properties from "./Properties";
import Descriptions from "./Descriptions";
import {context} from "../ContentList/context";
import Images from "./Images";

const styles = (theme) => ({
    appBar: {
        position: 'relative',
    },
    title: {
        marginLeft: theme.spacing(2),
        flex: 1,
    },
    margin: {
        width: "80%",
        margin: theme.spacing(3),
    },
});

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

class ContentForm extends React.Component {

    static contextType = context;

    constructor(props) {
        super(props);

        this.state = {
            active: 0,
            item: null,
        };
    }

    componentDidMount() {
        const
            {fetchProductItem} = this.context,
            {contentId} = this.props;

        fetchProductItem(contentId).then(res => {
            this.setState({item: res[0]});
        });
    }


    handleEditProperty(name) {
        return event => {
            const {target: {value}} = event;


            this.setState(state => {
                const {item} = state;

                return {
                    item: {
                        ...item,
                        property: {
                            RU: {
                                ...item.property.RU,
                                [name]: value,
                            }
                        }
                    }
                };
            });
        }
    }

    handleEditDescription(name) {
        return event => {
            const {target: {value}} = event;

            this.setState(state => {
                const {item} = state;

                return {
                    item: {
                        ...item,
                        description: {
                            RU: {
                                ...item?.description?.RU || {},
                                [name]: value,
                            }
                        }
                    }
                };
            });
        }
    }

    handleSave = () => {
        const
            {onClose} = this.props,
            {putProductItem} = this.context,
            {item} = this.state;

        console.log(item);
        
        
        putProductItem(item).then(() => {
            onClose();
        })
    }

    /**
     *
     */
    handleChange = (event, active) => this.setState({active});

    /**
     *
     */
    handleChangeIndex = active => this.setState({active});

    render() {
        const
            {onClose, classes} = this.props,
            {active, item} = this.state;

        if (!item) {
            return null;
        }

        return (
            <Dialog
                fullScreen
                open={true}
                onClose={onClose}
                TransitionComponent={Transition}
            >
                <AppBar className={classes.appBar}>
                    <Toolbar>
                        <IconButton
                            edge="start"
                            color="inherit"
                            onClick={onClose}
                            aria-label="close"
                        >
                            <CloseIcon/>
                        </IconButton>

                        <Typography variant="h6" className={classes.title}>
                            {"EDIT"}
                        </Typography>

                        <Button
                            autoFocus
                            color="inherit"
                            onClick={this.handleSave}
                        >
                            save
                        </Button>
                    </Toolbar>
                </AppBar>

                <div>
                    <Tabs
                        value={active}
                        onChange={this.handleChange}
                        indicatorColor="primary"
                        textColor="primary"
                    >
                        <Tab label={"Свойства"}/>
                        <Tab label={"Описания"}/>
                        <Tab label={"Изображения"}/>
                    </Tabs>

                    <SwipeableViews
                        axis={'x'}
                        index={active}
                        onChangeIndex={this.handleChangeIndex}
                    >
                        <Properties
                            list={item?.property?.RU  || {}}
                            handleEdit={this.handleEditProperty.bind(this)}
                        />

                        <Descriptions
                            list={item?.description?.RU  || {}}
                            handleEdit={this.handleEditDescription.bind(this)}
                        />

                        <Images
                            id={item.id}
                            list={item?.property?.RU  || {}}
                            handleEdit={this.handleEditProperty.bind(this)}
                        />
                    </SwipeableViews>
                </div>
            </Dialog>
        );
    }
}

export default withStyles(styles)(ContentForm);
