import React, {Component} from 'react';
import {
    Button,
    DialogActions,
    DialogContent,
    DialogTitle,
    TextField,
} from "@material-ui/core";
import context from "../context/FormContext";

class Form extends Component {
    static contextType = context;

    constructor(props) {
        super(props);

        this.state = {
            item: {
                group: "",
                method: "",
                entity: "",
            },
        };
    }

    componentDidMount() {
        const {edit, getItem} = this.props;

        if (edit) {
            getItem(edit)
                .then(item => this.setState({item}));
        }
    }

    handleEdit(field) {
        return event => {
            const
                {target: {value}} = event,
                {item} = this.state;

            this.setState({
                item: {
                    ...item,
                    [field]: value
                }
            });
        }
    }

    handleSave = () => {
        const {edit, onClose, postItem, putItem} = this.props;

        if (edit) {
            putItem(edit, this.state.item)
                .then(() => onClose());
        } else {
            postItem(this.state.item)
                .then(() => onClose());
        }
    }

    get renderTitle() {
        const {edit} = this.props;

        if (edit) {
            return `Edit property: ${edit}`;
        } else {
            return "New Property";
        }
    }

    render() {
        const
            {edit, onClose} = this.props,
            {item: {group, method, entity}} = this.state;

        return (
            <>
                <DialogTitle>
                    {this.renderTitle}
                </DialogTitle>

                <DialogContent>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="group"
                        label="Group"
                        type="text"
                        fullWidth
                        variant="standard"
                        value={group}
                        onChange={this.handleEdit("group")}
                    />

                    <TextField
                        margin="dense"
                        id="method"
                        label="Method"
                        type="text"
                        fullWidth
                        variant="standard"
                        value={method}
                        onChange={this.handleEdit("method")}
                    />

                    <TextField
                        margin="dense"
                        id="entity"
                        label="Entity"
                        type="text"
                        fullWidth
                        variant="standard"
                        value={entity}
                        onChange={this.handleEdit("entity")}
                    />
                </DialogContent>

                <DialogActions>
                    <Button onClick={onClose}>
                        Cancel
                    </Button>

                    <Button onClick={this.handleSave}>
                        Save
                    </Button>
                </DialogActions>
            </>
        );
    }
}

export {Form};

export default function(props) {
    const {permission} = React.useContext(context);

    return (
        <Form
            {...props}
            {...permission}
        />
    );
}
