import React from 'react';
import PropTypes from 'prop-types';


class Colors extends React.Component {
    /**
     *
     */
    renderItem(item) {
        const {setColor, current} = this.props;

        return (
            <span
                key={item}
                className={
                    current === item
                        ? `badge filter badge-${item} active`
                        : `badge filter badge-${item}`
                }
                data-color={item}
                onClick={() => setColor(item)}
            />
        );
    }

    /**
     *
     */
    renderList() {
        const {list} = this.props;

        return Object.keys(list).map(this.renderItem, this);
    }

    render() {
        return (

            <li className="adjustments-line">
                <a className="switch-trigger">
                    <div>
                        {this.renderList()}
                    </div>
                </a>
            </li>

        );
    }
}

Colors.propTypes = {};

export default Colors;
