import React, {Component} from 'react';
import {
    Button,
    DialogActions,
    DialogContent,
    DialogTitle, Tab, Tabs,
    TextField,
} from "@material-ui/core";
import context from "../context/FormContext";
import Fields from "./Fields";
import Parts from "./Parts";

class Form extends Component {
    static contextType = context;

    state = {
        item: {
            _id: "",
            name: "",
            field: [],
        },
    };

    componentDidMount() {
        const {edit, getItem} = this.props;

        if (edit) {
            getItem(edit)
                .then(item => this.setState({item}));
        }
    }

    handleEdit(field) {
        return event => {
            const
                {target: {value}} = event,
                {item} = this.state;

            this.setState({
                item: {
                    ...item,
                    [field]: value
                }
            });
        }
    }

    handleChangeField = fieldList => {
        const {item} = this.state;

        this.setState({item: {
            ...item,
            field: fieldList
        }});
    }

    renderField(field, disable) {
        const {item: {[field]: fieldValue}} = this.state;

        return (
            <TextField
                autoFocus
                margin="dense"
                id={field}
                label={field}
                type="text"
                fullWidth
                variant="standard"
                disabled={disable}
                value={fieldValue}
                onChange={this.handleEdit(field)}
            />
        );
    }

    handleSave = () => {
        const {edit, onClose, postItem, putItem} = this.props;

        if (edit) {
            putItem(edit, this.state.item)
                .then(() => onClose());
        } else {
            postItem(this.state.item)
                .then(() => onClose());
        }
    }

    get renderTitle() {
        const {edit} = this.props;

        if (edit) {
            return `Edit property: ${edit}`;
        } else {
            return "New Property";
        }
    }

    render() {
        const
            {edit, onClose} = this.props,
            {item: {_id, name, field}} = this.state;

        return (
            <>
                <DialogTitle>
                    {this.renderTitle}
                </DialogTitle>

                <DialogContent>
                    <Parts>
                        <div>
                            <TextField
                                autoFocus
                                margin="dense"
                                id="_id"
                                label="ID"
                                type="text"
                                fullWidth
                                variant="standard"
                                disabled={!!edit}
                                value={_id}
                                onChange={this.handleEdit("_id")}
                            />

                            <TextField
                                margin="dense"
                                id="name"
                                label="NAME"
                                type="text"
                                fullWidth
                                variant="standard"
                                value={name}
                                onChange={this.handleEdit("name")}
                            />
                        </div>

                        <Fields
                            onEdit={this.handleChangeField}
                            list={field}
                        />
                    </Parts>
                </DialogContent>

                <DialogActions>
                    <Button onClick={onClose}>
                        Cancel
                    </Button>

                    <Button onClick={this.handleSave}>
                        Save
                    </Button>
                </DialogActions>
            </>
        );
    }
}

export {Form};

export default function (props) {
    const {form} = React.useContext(context);

    return (
        <Form
            {...props}
            {...form}
        />
    );
}
