import React from 'react';
import DocumentTable from "../../components/DocumentTable";
import AddBoxIcon from "@material-ui/icons/AddBox";
import DeleteIcon from "@material-ui/icons/Delete";
import AutorenewIcon from "@material-ui/icons/Autorenew";
import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    TextField
} from "@material-ui/core";
import {contentContext} from "../../api/ContentApiContext";

class Section extends React.Component {
    static contextType = contentContext;

    state = {
        list: [],
        open: false,
    }

    componentDidMount() {
        this.fetchList();
    }

    fetchList() {
        const {section: {fetchSection}} = this.context;

        fetchSection().then(list => {
            console.log(list);

            this.setState({list})
        })
    }

    panel = [
        {
            icon: (
                <AddBoxIcon style={{color: '#039BE5'}}/>
            ),
            text: 'Add Item',
            onClick: () => {
                this.setState({open: true});
                // const {section: {postSection}} = this.context;

                // postSection()
                //     .then(() => {
                //         this.fetchList();
                //     });
            }
        },
        {
            icon: (
                <DeleteIcon style={{color: 'rgba(3, 155, 229,0.5)'}}/>
            ),
            text: 'Delete Item',
            onClick: () => {
            }
        },
        {
            icon: (
                <AutorenewIcon style={{color: 'rgba(3, 155, 229,0.5)'}}/>
            ),
            text: 'Change Item',
            onClick: () => {
            }
        }
    ];

    renderTitle = () => {
        return (
            <div>
                {"TITLE"}
            </div>
        );
    }

    columns = [
        {
            title: "ID"
        },
        {
            title: "SLUG"
        },
        {
            title: "ID"
        },
        {
            title: "ID"
        },
    ]

    renderCell = (column, row) => {
        const {list} = this.state;

        return list[row]._id;
    }

    renderHeaderCell = column => {
        return this.columns[column].title;
    }

    handleChangePage = () => {
        console.log("CLICK");
    }

    handleClose = () => {
        this.setState({open: false});
    }

    get renderPopup() {
        const {open} = this.state;

        return (
            <Dialog open={open} onClose={this.handleClose}>
                <DialogTitle>Subscribe</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        To subscribe to this website, please enter your email address here. We
                        will send updates occasionally.
                    </DialogContentText>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Email Address"
                        type="email"
                        fullWidth
                        variant="standard"
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={this.handleClose}>Cancel</Button>
                    <Button onClick={this.handleClose}>Subscribe</Button>
                </DialogActions>
            </Dialog>
        )
    }

    render() {
        const {list} = this.state;

        return (
            <div>
                <DocumentTable
                    renderTitle={this.renderTitle}
                    renderCell={this.renderCell}
                    renderHeaderCell={this.renderHeaderCell}
                    columns={4}
                    rows={list.length}
                    // rowsTotal={rowsTotal}
                    // panel={panel}
                    // page={10}
                    // rowsPerPage={rowsPerPage}
                    panel={this.panel}
                    onChangePage={this.handleChangePage}
                />

                {this.renderPopup}
            </div>
        );
    }
}

export default Section;
