import React, {Component} from 'react';
import {
    Button,
    DialogActions,
    DialogContent,
    DialogTitle,
} from "@material-ui/core";
import context from "../context/ContentContext";
import Parts from "./Parts";
import Fields from "./Fields";
import PropertyEdit from "../PropertyEdit";
import StatusEdit from "../StatusEdit";

class ContentForm extends Component {
    static contextType = context;

    state = {
        item: {
            slug: "",
            property: [],
            status: [],
            image: [],
        },
    };

    componentDidMount() {
        const {edit, getItem} = this.props;

        if (edit) {
            getItem(edit)
                .then(item => this.setState({item}));
        }
    }

    handleEdit = field => {
        return event => {
            const {target: {value}} = event;
            const {item} = this.state;

            this.setState({
                item: {
                    ...item,
                    [field]: value
                }
            });
        }
    }

    handleEditProperty = property => {
        this.setState(({item}) => ({
            item: {
                ...item,
                property,
            }
        }));
    }

    handleEditStatus = status => {
        console.log(status);
        
        
        this.setState(({item}) => ({
            item: {
                ...item,
                status,
            }
        }));
    }

    handleSave = () => {
        const {edit, onClose, postItem, putItem} = this.props;

        if (edit) {
            putItem(edit, this.state.item)
                .then(() => onClose());
        } else {
            postItem(this.state.item)
                .then(() => onClose());
        }
    }

    get renderTitle() {
        const {edit} = this.props;

        if (edit) {
            return `Edit content: ${edit}`;
        } else {
            return "New content";
        }
    }

    render() {
        const
            {edit, onClose} = this.props,
            {item: {_id, slug, property, status}} = this.state;

        return (
            <>
                <DialogTitle>
                    {this.renderTitle}
                </DialogTitle>

                <DialogContent>
                    <Parts>
                        <Fields
                            _id={_id}
                            slug={slug}
                            edit={edit}
                            onEdit={this.handleEdit}
                        />

                        <PropertyEdit
                            onEdit={this.handleEditProperty}
                            values={property}
                        />

                        <StatusEdit
                            onEdit={this.handleEditStatus}
                            values={status}
                        />
                    </Parts>
                </DialogContent>

                <DialogActions>
                    <Button onClick={onClose}>
                        Cancel
                    </Button>

                    <Button onClick={this.handleSave}>
                        Save
                    </Button>
                </DialogActions>
            </>
        );
    }
}

export {ContentForm};

export default function (props) {
    const {content} = React.useContext(context);

    return (
        <ContentForm
            {...props}
            {...content}
        />
    );
}
