/*eslint-disable*/
import React from "react";
import classNames from "classnames";
import PropTypes from "prop-types";
import {NavLink, useLocation} from "react-router-dom";
// @material-ui/core components
import {makeStyles} from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Icon from "@material-ui/core/Icon";
// core components
import AdminNavbarLinks from "components/Navbars/AdminNavbarLinks.js";
import RTLNavbarLinks from "components/Navbars/RTLNavbarLinks.js";

import styles from "assets/jss/material-dashboard-react/components/sidebarStyle.js";
import {themeContext} from "../../theme/SettingsProvider";

const useStyles = makeStyles(styles);

export default function Sidebar(props) {
    const classes = useStyles();
    let location = useLocation();

    function activeRoute(routeName) {
        return location.pathname === routeName;
    }

    const {logo, logoText, routes} = props;
    const theme = React.useContext(themeContext);

    var links = routes.map((prop, key) => {
        var activePro = " ";
        var listItemClasses;

        listItemClasses = classNames({
            [" " + classes[theme.color.current]]: activeRoute(prop.layout + prop.path),
        });

        const whiteFontClasses = classNames({
            [" " + classes.whiteFont]: activeRoute(prop.layout + prop.path),
        });

        return (
            <NavLink
                to={prop.layout + prop.path}
                className={activePro + classes.item}
                activeClassName="active"
                key={key}
            >
                <ListItem button className={classes.itemLink + listItemClasses}>
                    {typeof prop.icon === "string" ? (
                        <Icon
                            className={classNames(classes.itemIcon, whiteFontClasses, {
                                [classes.itemIconRTL]: props.rtlActive,
                            })}
                        >
                            {prop.icon}
                        </Icon>
                    ) : (
                        <prop.icon
                            className={classNames(classes.itemIcon, whiteFontClasses, {
                                [classes.itemIconRTL]: props.rtlActive,
                            })}
                        />
                    )}

                    <ListItemText
                        primary={props.rtlActive ? prop.rtlName : prop.name}
                        className={classNames(classes.itemText, whiteFontClasses, {
                            [classes.itemTextRTL]: props.rtlActive,
                        })}
                        disableTypography={true}
                    />
                </ListItem>
            </NavLink>
        );
    });

    var brand = (
        <div className={classes.logo}>
            <a
                href="https://www.creative-tim.com?ref=mdr-sidebar"
                className={classNames(classes.logoLink, {
                    [classes.logoLinkRTL]: props.rtlActive,
                })}
                target="_blank"
            >
                <div className={classes.logoImage}>
                    <img src={logo} alt="logo" className={classes.img}/>
                </div>
                {logoText}
            </a>
        </div>
    );

    return (
        <div>
            <Hidden mdUp implementation="css">
                <Drawer
                    variant="temporary"
                    anchor={props.rtlActive ? "left" : "right"}
                    open={props.open}
                    classes={{
                        paper: classNames(classes.drawerPaper, {
                            [classes.drawerPaperRTL]: props.rtlActive,
                        }),
                    }}
                    onClose={props.handleDrawerToggle}
                    ModalProps={{
                        keepMounted: true, // Better open performance on mobile.
                    }}
                >
                    {brand}

                    <div className={classes.sidebarWrapper}>
                        {props.rtlActive ? <RTLNavbarLinks/> : <AdminNavbarLinks/>}

                        <List className={classes.list}>
                            {links}
                        </List>
                    </div>

                    {theme.image.current !== undefined ? (
                        <div
                            className={classes.background}
                            style={{backgroundImage: "url(" + theme.image.list[theme.image.current] + ")"}}
                        />
                    ) : null}
                </Drawer>
            </Hidden>

            <Hidden smDown implementation="css">
                <Drawer
                    anchor={props.rtlActive ? "right" : "left"}
                    variant="permanent"
                    open
                    classes={{
                        paper: classNames(classes.drawerPaper, {
                            [classes.drawerPaperRTL]: props.rtlActive,
                        }),
                    }}
                >
                    {brand}

                    <div className={classes.sidebarWrapper}>
                        <List className={classes.list}>
                            {links}
                        </List>
                    </div>

                    {theme.image.current !== undefined ? (
                        <div
                            className={classes.background}
                            style={{backgroundImage: "url(" + theme.image.list[theme.image.current] + ")"}}
                        />
                    ) : null}
                </Drawer>
            </Hidden>
        </div>
    );
}

Sidebar.propTypes = {
    rtlActive: PropTypes.bool,
    handleDrawerToggle: PropTypes.func,
    bgColor: PropTypes.oneOf(["purple", "blue", "green", "orange", "red"]),
    logo: PropTypes.string,
    image: PropTypes.string,
    logoText: PropTypes.string,
    routes: PropTypes.arrayOf(PropTypes.object),
    open: PropTypes.bool,
};
