import React, {Component} from 'react';
import routes from "routes.js";
import ContentList from "../views/ContentList";
import Doppelganger from "../doppelganger";
import Admin from "./Admin";
import Groot from "../groot";

const modules = {
    groot: {
        component: Groot,
        layout: "/admin",
        icon: "content_paste",
    },
    doppelganger: {
        component: Doppelganger,
        layout: "/admin",
        icon: "content_paste",
    }
};

class Modules extends Component {
    state = {
        list: [],
    };

    componentDidMount() {
        fetch(process.env.PUBLIC_URL + "/services.json")
            .then(result => result.json())
            .then(list => this.setState({list}))
            .catch(reason => {
                console.error(reason);
            })
    }

    parseModule(item) {
        const module = modules[item.type]

        return {
            path: item.path,
            name: item.name,
            icon: module.icon,
            component: module.component,
            layout :module.layout,
            url: item.url,
        };
    }

    render() {
        const {list} = this.state;

        return (
            <Admin
                routes={routes.concat(list.map(this.parseModule))}
            />
        );
    }
}

export default Modules;
