import React, {Component} from 'react';
import DocumentTable from "../../components/DocumentTable";
import Form from "./Form";
import {Dialog} from "@material-ui/core";
import {contentContext} from "../../api/ContentApiContext";
import columns from "./columns";
import panel from "./panel";


class PropertyList extends Component {
    state = {
        list: [],
        open: false,
        edit: null,
    };

    panel = panel.call(this)

    columns = columns;

    componentDidMount() {
        this.fetchList();
    }

    fetchList() {
        const {getPropertyList} = this.props;

        getPropertyList().then(list => this.setState({list}));
    }

    renderTitle = () => {
        return (
            <div>
                {"TITLE"}
            </div>
        );
    }

    renderCell(column, row) {
        return this.columns[column].render.call(this, row);
    }

    renderHeaderCell = column => this.columns[column].title;

    handleChangePage = () => {
        console.log("CLICK");
    }

    handleClose = () => {
        this.setState({open: false, edit: null});
        this.fetchList();
    }

    get renderPopup() {
        const {open, edit} = this.state;

        return (
            <Dialog
                open={open}
                onClose={this.handleClose}
            >
                <Form
                    key={edit}
                    edit={edit}
                    onClose={this.handleClose}
                />
            </Dialog>
        );
    }

    render() {
        const {list} = this.state;

        return (
            <div>
                <DocumentTable
                    renderTitle={this.renderTitle}
                    renderCell={this.renderCell.bind(this)}
                    renderHeaderCell={this.renderHeaderCell}
                    columns={4}
                    rows={list.length}
                    // rowsTotal={rowsTotal}
                    // panel={panel}
                    // page={10}
                    // rowsPerPage={rowsPerPage}
                    panel={this.panel}
                    onChangePage={this.handleChangePage}
                />

                {this.renderPopup}
            </div>
        );
    }
}

export {PropertyList};

export default function() {
    const {property} = React.useContext(contentContext);

    return (
        <PropertyList
            {...property}
        />
    );
}
