import React from 'react';
import {Button, Container, Dialog, DialogActions, DialogContent, DialogTitle, Slide} from "@material-ui/core";
import DocumentTable from "../../components/DocumentTable";
import PropertyForm from "../PropertyForm";
import columns from "./columns";
import panel from "./panel";
import context from "../context/ContentContext";


class PropertyList extends React.Component {
    state = {
        list: [],
        open: false,
        edit: null,
    };

    panel = panel.call(this);

    columns = columns.call(this);

    componentDidMount = () => this.fetchList();

    fetchList() {
        const {getList} = this.props;

        getList().then(list => this.setState({list}));
    }

    renderCell = (column, row) => this.columns[column].render(row);

    renderHeaderCell = column => this.columns[column].title;

    handleClose = () => {
        this.setState({open: false, edit: null});
        this.fetchList();
    }

    get renderTitle() {
        const {edit} = this.props;

        if (edit) {
            return `Edit property: ${edit}`;
        } else {
            return "New property";
        }
    }

    render() {
        const {open, edit, list} = this.state;

        return (
            <Container>
                <DocumentTable
                    renderTitle="Список cвойств"
                    renderCell={this.renderCell}
                    renderHeaderCell={this.renderHeaderCell}
                    columns={this.columns.length}
                    rows={list.length}
                    panel={this.panel}
                />

                <Dialog
                    open={open}
                    onClose={this.handleClose}
                >
                    <PropertyForm
                        key={edit}
                        edit={edit}
                        onClose={this.handleClose}
                    />
                </Dialog>
            </Container>
        );
    }
}

export default function (props) {
    const {property} = React.useContext(context);

    return (
        <PropertyList
            {...props}
            {...property}
        />
    );
}
