import EditIcon from "@material-ui/icons/Edit";
import React from "react";
import {Button} from "@material-ui/core";

export default function () {
    return [
        {
            title: "",
            render: row => {
                return (
                    <Button onClick={() => this.setState({
                        open: true,
                        edit: this.state.list[row]._id,
                    })}>
                        <EditIcon style={{color: 'rgba(3, 155, 229,0.5)'}} />
                    </Button>
                );
            }
        },
        {
            title: "ID",
            render: row => {
                const {list} = this.state;

                return list[row]._id
            },
        },
        {
            title: "Время последнего изменения",
            render: row => {
                const {list} = this.state;

                return list[row].timestamp
            },
        },
        {
            title: "Свойства",
            render: row => {
                const {list} = this.state;

                return list[row].property.map(item => item.property + ">>>" + item.value).join(", ");
            },
        },
        {
            title: "Статусы",
            render: row => {
                const {list} = this.state;

                return list[row].status.join(", ");
            },
        },
    ];
}
