import React from 'react';
import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import Paper from '@material-ui/core/Paper';
import CardHeader from "../Card/CardHeader";
import DrawerTable from "./DrawerTable";
import TableBody from "@material-ui/core/TableBody";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import Cell from "./Cell"
import TablePagination from '@material-ui/core/TablePagination';

class DocumentTable extends React.Component {
    renderCellHeader() {
        const {columns, renderHeaderCell, classes} = this.props;
        const cellArray = [];

        for (let i = 0; i < columns; i++) {
            cellArray.push(
                <TableCell className={classes.table_cell_header} key={i}>
                    {renderHeaderCell(i)}
                </TableCell>
            )
        }

        return cellArray;
    }

    renderCellTable() {
        const {columns, rows, renderCell} = this.props;
        const rowArray = [];

        for (let rowNumber = 0; rowNumber < rows; rowNumber++) {
            rowArray.push(
                <TableRow key={rowNumber}>
                    <Cell
                        renderCell={renderCell}
                        columns={columns}
                        rowNumber={rowNumber}
                    />
                </TableRow>
            );
        }

        return rowArray;
    }

    renderCardHeader() {
        const {classes, renderTitle} = this.props;

        if (!renderTitle) {
            return null;
        }
        let title = "";

        if (typeof renderTitle === "function") {
            title = renderTitle()
        } else {
            title = renderTitle;
        }

        return (
            <CardHeader
                className={classes.card_header}
                color="primary"
            >
                {title}
            </CardHeader>
        );
    }

    renderDrawerTable() {
        const {panel} = this.props;

        if (!panel) {
            return null;
        }

        return (
            <DrawerTable
                panel={panel}
            />
        );
    }

    renderPagination() {
        const {rowsTotal, page, onChangePage} = this.props;

        if (rowsTotal && onChangePage && Number.isInteger(page)) {
            return (
                <TablePagination
                    rowsPerPageOptions={[]}
                    component="div"
                    count={rowsTotal}
                    rowsPerPage={10}
                    page={page}
                    onChangePage={onChangePage}
                />
            )
        } else return null
    }

    render() {
        const {classes} = this.props;

        return (
            <div className={classes.wrapper_document_table}>
                {this.renderCardHeader()}

                <TableContainer
                    className={classes.table_container}
                    component={Paper}
                >
                    <Table
                        className={classes.table}
                        aria-label="simple table"
                    >
                        <TableHead>
                            <TableRow>
                                {this.renderCellHeader()}
                            </TableRow>
                        </TableHead>

                        <TableBody>
                            {this.renderCellTable()}
                        </TableBody>
                    </Table>

                    {this.renderDrawerTable()}
                </TableContainer>

                {this.renderPagination()}
            </div>
        );
    }
}

export default withStyles(theme => {
    // console.log(theme.palette.primary);


    return {
        wrapper_document_table: {
            backgroundColor: 'white',
            margin: '50px 5px 5px 5px',
            padding: '0 0 0 25px',
            borderRadius: 10
        },

        card_header: {
            top: '-20px',
            margin: '0 25px 0 0' + '!important',
        },

        table: {
            minWidth: 650,
        },

        table_container: {
            boxShadow: 'none',
            display: 'flex'
        },

        table_cell_header: {
            color: theme.palette.primary.main,
        },
    }
})(DocumentTable);
