import React from 'react';
import {TextField} from "@material-ui/core";

function Fields(props) {
    const {edit, onEdit, _id} = props;
    const handleEdit = event => {
        onEdit("_id")(event.target.value);
    }

    return (
        <TextField
            margin="dense"
            id="ID"
            label="ID"
            type="text"
            fullWidth
            variant="standard"
            disabled={!!edit}
            value={_id || ""}
            onChange={handleEdit}
        />
    );
}

export default Fields;
