import React from 'react';
import {Checkbox, FormControlLabel, Grid, TextField} from "@material-ui/core";

function Item(props) {
    const {onEdit, checked, label} = props;

    return (
        <Grid item xs={6} md={3}>
            <FormControlLabel
                control={
                    <Checkbox
                        id={label}
                        label={label}
                        checked={checked}
                        onChange={onEdit}
                    />
                }
                label={label}
            />
        </Grid>
    );
}

export default Item;
