
export function getStatusList() {
    return this.fetchContent("status")
}

export function getStatusItem(id) {
    return fetch(
        `http://localhost:3003/status/${id}/`,
    ).then(res => res.json())
}

export function postStatus(item) {
    return fetch(
        `http://localhost:3003/status/`,
        {
            method: "POST",
            body: JSON.stringify(item),
            headers: {
                'Content-Type': 'application/json',
            }
        }
    ).then(res => res.json())
}

export function putStatus(item) {
    return fetch(
        `http://localhost:3003/status/${item._id}/`,
        {
            method: "PUT",
            body: JSON.stringify(item),
            headers: {
                'Content-Type': 'application/json',
            }
        }
    ).then(res => res.json())
}
