import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import {withStyles} from "@material-ui/core/styles";
import context from "../context/ContentContext";
import Item from "./Item";

class StatusEdit extends Component {
    constructor(props) {
        super(props);

        this.state = {
            list: [],
        };
    }

    componentDidMount() {
        const {getList} = this.props;

        getList().then(list => this.setState({list}));
    }

    handleEdit = status => {
        const {onEdit, values} = this.props;

        return () => {
            const edit = Array.from(values);

            for (const key in edit) {
                if (edit[key] === status) {
                    edit.splice(key, 1);

                    onEdit(edit);
                    return;
                }
            }

            edit.push(status);

            onEdit(edit);
        }
    }

    renderLine(line, index) {
        const
            {_id} = line,
            {values} = this.props;

        const value = values.find(item => item === _id);

        return (
            <Item
                onEdit={this.handleEdit(_id, index)}
                checked={!!value}
                label={_id}
                key={_id}
            />
        );
    }

    renderList() {
        const {list} = this.state;

        return list.map(this.renderLine, this);
    }

    render() {
        const {classes} = this.props;

        return (
            <Grid
                container
                spacing={4}
                className={classes.wrap}
            >
                {this.renderList()}
            </Grid>
        );
    }
}



export default withStyles(theme => ({
    wrap: {
        padding: "15px 0"
    }
}))(function (props) {
    const {status} = React.useContext(context);

    return (
        <StatusEdit
            {...props}
            {...status}
        />
    );
});
