import React from 'react';
import {TextField} from "@material-ui/core";

function Fields(props) {
    const {edit, onEdit, slug, _id} = props;

    return (
        <div>
            <TextField
                autoFocus
                margin="dense"
                id="_id"
                label="ID"
                type="text"
                fullWidth
                variant="standard"
                disabled={true}
                value={_id}
                onChange={onEdit("_id")}
            />

            <TextField
                margin="dense"
                id="name"
                label="Slug"
                type="text"
                fullWidth
                variant="standard"
                value={slug || ""}
                onChange={onEdit("slug")}
            />
        </div>
    );
}

export default Fields;
