import React from 'react';
import {Grid, TextField} from "@material-ui/core";

function Item(props) {
    const {onEdit, value, label} = props;

    return (
        <Grid item xs={12} md={6}>
            <TextField
                margin="dense"
                id={label}
                label={label}
                type="text"
                fullWidth
                value={value || ""}
                onChange={onEdit}
            />
        </Grid>
    );
}

export default Item;
