import React, {Component} from 'react';
import DocumentTable from "../../components/DocumentTable";
import Form from "./Form";
import {Dialog} from "@material-ui/core";
import context from "../context/FormContext";
import columns from "./columns";
import panel from "./panel";

class PermissionList extends Component {
    state = {
        list: [],
        open: false,
        edit: null,
    };

    panel = panel.call(this)

    columns = columns.call(this);

    componentDidMount = () => this.fetchList();

    fetchList() {
        const {getList} = this.props;

        getList().then(list => this.setState({list}))
    }

    renderCell = (column, row) => this.columns[column].render.call(this, row);

    renderHeaderCell = column => this.columns[column].title;

    handleClose = () => {
        this.setState({open: false, edit: null});
        this.fetchList();
    }

    render() {
        const {list, open, edit} = this.state;

        return (
            <div>
                <DocumentTable
                    renderTitle="Доступы к формам"
                    renderCell={this.renderCell}
                    renderHeaderCell={this.renderHeaderCell}
                    columns={this.columns.length}
                    rows={list.length}
                    panel={this.panel}
                />

                <Dialog
                    open={open}
                    onClose={this.handleClose}
                >
                    <Form
                        key={edit}
                        edit={edit}
                        onClose={this.handleClose}
                    />
                </Dialog>
            </div>
        );
    }
}

export {PermissionList};

export default function() {
    const {permission} = React.useContext(context);

    return (
        <PermissionList
            {...permission}
        />
    );
}
