import React from 'react';
import PropTypes from 'prop-types';
import {withStyles} from "@material-ui/core/styles";
import {Input} from "@material-ui/core";
import {context} from "../ContentList/context";


class Images extends React.Component {

    static contextType = context;

    handleChange = ({target}) => {
        const {id, handleEdit} = this.props;
        const {uploadFile} = this.context;

        const fileReader = new FileReader();
        const name = target.accept.includes('image') ? 'images' : 'videos';

        fileReader.readAsDataURL(target.files[0]);
        fileReader.onload = (e) => {
            console.log(e.target);

            uploadFile(`image_${id}.jpg`, e.target.result)
                .then(filename => {
                    handleEdit("image")({target: {value: filename}});
                })
            
            // this.setState((prevState) => ({
            //     [name]: [...prevState[name], e.target.result]
            // }));
        };
    }

    get renderInput() {
        const {classes, property: {image} = {}} = this.props;

        return (
            <Input
                className={classes.margin}
                type="file"
                onChange={this.handleChange}
            />
        );
    }

    render() {
        const {classes} = this.props;

        return (
            <form className={classes.root} noValidate autoComplete="off">
                {this.renderInput}
            </form>
        );
    }
}

Images.propTypes = {};

export default withStyles(theme => ({
    margin: {
        width: "80%",
        margin: theme.spacing(3),
    },
}))(Images);
