import React from "react";

export const context = React.createContext({});

export class Context extends React.Component {

    fetchData(entity, filter) {
        return fetch(
            `http://3.66.192.90/${entity}/?${filter}`,
            {
                headers: {
                    authorization: "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJhZG1pbiI6dHJ1ZSwiZ3JvdXBzIjpbXSwiaWQiOjExfQ.qXWtzl3eD2M2dAcYMKgXNdZ_73srumAtrtCuHw9rErs"
                }
            }
        ).then(res => res.json())
    }

    fetchUserList() {
        return this.fetchData("user", "")
    }

    render() {
        const {children} = this.props;

        return (
            <context.Provider value={{
                fetchUserList: this.fetchUserList.bind(this),
            }}>
                {children}
            </context.Provider>
        );
    }
}

