import React from 'react';
import {TextField} from "@material-ui/core";

function Fields(props) {
    const {edit, onEdit, _id} = props;

    return (
        <div>
            <TextField
                margin="dense"
                id="name"
                label="ID"
                type="text"
                fullWidth
                variant="standard"
                disabled={!!edit}
                value={_id || ""}
                onChange={onEdit("_id")}
            />
        </div>
    );
}

export default Fields;
