import React, {Component} from 'react';
import {context} from "../ContentList/context";
import AddBoxIcon from "@material-ui/icons/AddBox";
import DeleteIcon from "@material-ui/icons/Delete";
import EditIcon from  "@material-ui/icons/Edit";
import AutorenewIcon from "@material-ui/icons/Autorenew";
import DocumentTable from "../../components/DocumentTable";
import Form from "./Form";
import {Dialog} from "@material-ui/core";
import {contentContext} from "../../api/ContentApiContext";
import columns from "./columns";
import panel from "./panel";

class StatusList extends Component {
    static contextType = contentContext;

    state = {
        list: [],
        open: false,
        edit: null,
    }

    columns = columns;

    panel = panel;

    componentDidMount() {
        this.fetchList();
    }

    fetchList() {
        const {status: {getStatusList}} = this.context;

        getStatusList().then(list => this.setState({list}));
    }


    renderTitle = () => {
        return (
            <div>
                {"TITLE"}
            </div>
        );
    }


    renderCell = (column, row) => {
        return this.columns[column].render.call(this, row);
    }

    renderHeaderCell = column => {
        return this.columns[column].title;
    }

    handleChangePage = () => {
        console.log("CLICK");
    }

    handleClose = () => {
        this.setState({open: false});
    }

    get renderPopup() {
        const {open, edit} = this.state;

        return (
            <Dialog
                open={open}
                onClose={this.handleClose}
            >
                <Form
                    edit={edit}
                    onClose={this.handleClose}
                />
            </Dialog>
        );
    }

    render() {
        const {list} = this.state;

        return (
            <div>
                <DocumentTable
                    renderTitle={this.renderTitle}
                    renderCell={this.renderCell}
                    renderHeaderCell={this.renderHeaderCell}
                    columns={4}
                    rows={list.length}
                    // rowsTotal={rowsTotal}
                    // panel={panel}
                    // page={10}
                    // rowsPerPage={rowsPerPage}
                    panel={this.panel}
                    onChangePage={this.handleChangePage}
                />

                {this.renderPopup}
            </div>
        );
    }
}

export default StatusList;
