export function getPropertyList() {
    return this.fetchContent("property")
}

export function getPropertyItem(id) {
    return fetch(
        `http://localhost:3003/property/${id}/`,
    ).then(res => res.json())
}

export function postProperty(item) {
    return fetch(
        `http://localhost:3003/property/`,
        {
            method: "POST",
            body: JSON.stringify(item),
            headers: {
                'Content-Type': 'application/json',
            }
        }
    ).then(res => res.json())
}

export function putProperty(item) {
    return fetch(
        `http://localhost:3003/property/${item._id}/`,
        {
            method: "PUT",
            body: JSON.stringify(item),
            headers: {
                'Content-Type': 'application/json',
            }
        }
    ).then(res => res.json())
}
