import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import {withStyles} from "@material-ui/core/styles";
import context from "../context/ContentContext";
import Item from "./Item";

class PropertyEdit extends Component {
    constructor(props) {
        super(props);

        this.state = {
            list: [],
        };
    }

    componentDidMount() {
        const {getList} = this.props;

        getList().then(list => this.setState({list}));
    }

    handleEdit = (property, index) => {
        const {onEdit, values} = this.props;

        return event => {
            const edit = Array.from(values);
            const {value} = event.target;

            for (const key in edit) {
                if (edit[key].property === property) {
                    if (value) {
                        edit[key] = {
                            property: property,
                            value,
                        };
                    } else {
                        edit.splice(key, 1);
                    }

                    onEdit(edit);
                    return;
                }
            }

            edit.push({
                property: property,
                value: event.target.value
            });

            onEdit(edit);
        }
    }

    renderLine(line, index) {
        const
            {_id} = line,
            {values} = this.props;

        const value = values.find(item => item.property === _id);

        return (
            <Item
                onEdit={this.handleEdit(_id, index)}
                value={value?.value}
                label={_id}
                key={_id}
            />
        );
    }

    renderList() {
        const {list} = this.state;

        return list.map(this.renderLine, this);
    }

    render() {
        const {classes} = this.props;

        return (
            <Grid
                container
                spacing={2}
                className={classes.wrap}
            >
                {this.renderList()}
            </Grid>
        );
    }
}

export default withStyles(theme => ({
    wrap: {
        padding: "15px 0"
    }
}))(function (props) {
    const {property} = React.useContext(context);

    return (
        <PropertyEdit
            {...props}
            {...property}
        />
    );
});
