import React, {Component} from 'react';
import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    TextField,
    Checkbox,
    FormControlLabel, FormGroup, Grid
} from "@material-ui/core";
import {contentContext} from "../../api/ContentApiContext";

class Form extends Component {
    static contextType = contentContext;

    constructor(props) {
        super(props);

        this.state = {
            item: {
                _id: "",
                status: [],
                property: [],
            },
            statusList: [],
            propertyList: [],
        };
    }

    componentDidMount() {
        const
            {
                property: {getPropertyItem, getPropertyList},
                status: {getStatusList},
            } = this.context,
            {edit} = this.props;

        getStatusList()
            .then(statusList => this.setState({statusList}));

        getPropertyList()
            .then(propertyList => this.setState({propertyList}));

        if (edit) {
            getPropertyItem(edit)
                .then(item => {
                    this.setState({item})
                });
        }
    }

    handleEdit(field) {
        return event => {
            const
                {target: {value}} = event,
                {item} = this.state;

            this.setState({
                item: {
                    ...item,
                    [field]: value
                }
            });
        }
    }

    get renderSlug() {
        const
            {item: {_id}} = this.state,
            {edit} = this.props;

        return (
            <TextField
                autoFocus
                margin="dense"
                id="name"
                label="Status slug"
                type="text"
                fullWidth
                variant="standard"
                disabled={!!edit}
                value={_id}
                onChange={this.handleEdit("_id")}
            />
        );
    }

    handleChangeStatus = event => {
        const
            {target: {value}} = event,
            {item} = this.state;

        if (item.status.includes(value)) {
            const index = item.status.findIndex(item => item === value);
            const reduced = [...item.status];
            reduced.splice(index, 1);

            this.setState({
                item: {
                    ...item,
                    status: reduced
                }
            });
        } else {
            this.setState({
                item: {
                    ...item,
                    status: [...item.status, value]
                }
            });
        }
    }

    get renderStatusList() {
        const {statusList, item: {status}} = this.state;

        return statusList.map(item => {
            return (
                <Grid item xs={4}>
                    <FormControlLabel label={item._id} control={
                        <Checkbox
                            label={item._id}
                            value={item._id}
                            onChange={this.handleChangeStatus}
                            checked={status.includes(item._id)}
                        />
                    }/>
                </Grid>
            );
        });
    }

    handleChangeProperty = event => {
        const {item} = this.state;
        const index = item.property.findIndex(el => el.property === event.target.name);

        if (~index) {
            const property = [...item.property];
            property[index] = {
                property: event.target.name,
                value: event.target.value,
            }

            this.setState({
                item: {
                    ...item,
                    property,
                }
            });
        } else {
            this.setState({
                item: {
                    ...item,
                    property: [
                        ...item.property,
                        {
                            property: event.target.name,
                            value: event.target.value,
                        }
                    ]
                }
            });
        }
    }

    get renderPropertyList() {
        const {propertyList, item: {property}} = this.state;

        return propertyList.map(item => {
            const value = property.find(el => el.property === item._id)?.value ?? "";

            return (
                <Grid item xs={6}>
                    <TextField
                        margin="dense"
                        id="name"
                        label={item._id}
                        type="text"
                        fullWidth
                        name={item._id}
                        variant="standard"
                        value={value}
                        onChange={this.handleChangeProperty}
                    />
                </Grid>
            );
        });
    }

    handleSave = item => {
        const
            {property: {postProperty, putProperty}} = this.context,
            {edit, onClose} = this.props;

        if (edit) {
            putProperty(this.state.item)
                .then(() => onClose());
        } else {
            postProperty(this.state.item)
                .then(() => onClose());
        }
    }

    get renderTitle() {
        const {edit} = this.props;

        if (edit) {
            return `Edit property: ${edit}`;
        } else {
            return "New Property";
        }
    }

    render() {
        const {onClose} = this.props;

        return (
            <>
                <DialogTitle>
                    {this.renderTitle}
                </DialogTitle>

                <DialogContent>
                    {/*<DialogContentText>*/}
                    {/*    To subscribe to this website, please enter your email address here. We*/}
                    {/*    will send updates occasionally.*/}
                    {/*</DialogContentText>*/}

                    {this.renderSlug}

                    <Grid container spacing={2}>
                        {this.renderStatusList}
                    </Grid>

                    <Grid container spacing={2}>
                        {this.renderPropertyList}
                    </Grid>
                </DialogContent>

                <DialogActions>
                    <Button onClick={onClose}>
                        Cancel
                    </Button>

                    <Button onClick={this.handleSave}>
                        Save
                    </Button>
                </DialogActions>
            </>
        );
    }
}

export default Form;
