import React, {Component} from 'react';
import {
    Button,
    DialogActions,
    DialogContent,
    DialogTitle,
    TextField,
} from "@material-ui/core";
import context from "../context/FormContext";

class Form extends Component {
    static contextType = context;

    constructor(props) {
        super(props);

        this.state = {
            item: {
                _id: "",
                name: "",
            },
        };
    }

    componentDidMount() {
        const {edit, getItem} = this.props;

        if (edit) {
            getItem(edit)
                .then(item => this.setState({item}));
        }
    }

    handleEdit(field) {
        return event => {
            const
                {target: {value}} = event,
                {item} = this.state;

            this.setState({
                item: {
                    ...item,
                    [field]: value
                }
            });
        }
    }

    renderField(field, disable) {
        const {item: {[field]: fieldValue}} = this.state;

        return (
            <TextField
                autoFocus
                margin="dense"
                id={field}
                label={field}
                type="text"
                fullWidth
                variant="standard"
                disabled={disable}
                value={fieldValue}
                onChange={this.handleEdit(field)}
            />
        );
    }

    handleSave = () => {
        const {edit, onClose, postResultItem, putResultItem} = this.props;

        if (edit) {
            putResultItem(this.state.item)
                .then(() => onClose());
        } else {
            postResultItem(this.state.item)
                .then(() => onClose());
        }
    }

    get renderTitle() {
        const {edit} = this.props;

        if (edit) {
            return `Edit property: ${edit}`;
        } else {
            return "New Property";
        }
    }

    render() {
        const {edit, onClose} = this.props;

        return (
            <>
                <DialogTitle>
                    {this.renderTitle}
                </DialogTitle>

                <DialogContent>
                    {this.renderField("_id", !!edit)}

                    {this.renderField("name")}
                </DialogContent>

                <DialogActions>
                    <Button onClick={onClose}>
                        Cancel
                    </Button>

                    <Button onClick={this.handleSave}>
                        Save
                    </Button>
                </DialogActions>
            </>
        );
    }
}

export {Form};

export default function(props) {
    const {result} = React.useContext(context);

    return (
        <Form
            {...props}
            {...result}
        />
    );
}
