class Products extends React.Component {
    static contextType = contentContext;

    state = {
        list: [],
        edit: null,
    };

    componentDidMount() {
        this.fetchList();
    }

    fetchList() {
        const {fetchProductList} = this.context;

        fetchProductList().then(result => {
            this.setState({
                list: result,
            });
        });
    }

    handleClick = row => {
        this.setState({edit: row.id});
    };

    get renderGrid() {
        const {list} = this.state;

        return (
            <DataGrid
                rows={list}
                columns={columns}
                // pageSize={500}
                checkboxSelection
                disableSelectionOnClick
                onRowDoubleClick={this.handleClick}
            />
        );
    }

    get renderPopup() {
        const {edit} = this.state;

        if (!edit) {
            return null;
        }

        return (
            <ContentForm
                contentId={edit}
                onClose={() => {
                    this.setState({edit: null});
                    this.fetchList();
                }}
            />
        );
    }


    handleUpload = () => {

    }

    render() {
        return (
            <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                    <Box m="2rem">
                        <ButtonGroup variant="text">
                            <Button onClick={this.handleUpload}>
                                {"ADD"}
                            </Button>

                            <Button>
                                {"ADD"}
                            </Button>

                            <Button>
                                {"ADD"}
                            </Button>
                        </ButtonGroup>
                    </Box>

                    <Card>
                        <CardHeader color="primary">
                            <div>
                                Product List
                            </div>
                        </CardHeader>

                        <CardBody>
                            <div style={{height: 1000, width: '100%'}}>
                                {this.renderGrid}
                            </div>
                        </CardBody>
                    </Card>

                    {this.renderPopup}
                </GridItem>
            </GridContainer>
        );
    }
}

Products.propTypes = {};

export default Products;
