import React, {Component} from 'react';
import {Button, Container, Grid, TextField} from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import {withStyles} from "@material-ui/core/styles";

class Fields extends Component {

    handleEdit = (field, index) => {
        const {list, onEdit} = this.props;

        return event => {
            const edit = Array.from(list);

            edit[index] = {
                ...edit[index],
                [field]: event.target.value
            };

            onEdit(edit);
        }
    }

    handleAdd = () => {
        const {list, onEdit} = this.props;

        onEdit([...list, {}])
    }

    handleDelete(index) {
        const {list, onEdit} = this.props;

        return () => {
            const edit = Array.from(list);
            edit.splice(index, 1);

            onEdit(edit);
        }
    }
    
    renderLine(line, index) {
        const {name, slug} = line;

        return (
            <>
                <Grid item xs={2}>
                    <Button
                        name={"DELETE"}
                        onClick={this.handleDelete(index)}
                        variant="outlined"
                    >
                        <DeleteIcon
                            style={{color: 'rgba(3, 155, 229,0.5)'}}
                        />
                    </Button>
                </Grid>

                <Grid item xs={5}>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="NAME"
                        type="text"
                        fullWidth
                        variant="standard"
                        value={name}
                        onChange={this.handleEdit("name", index)}
                    />
                </Grid>

                <Grid item xs={5}>
                    <TextField
                        margin="dense"
                        id="slug"
                        label="SLUG"
                        type="text"
                        fullWidth
                        variant="standard"
                        value={slug}
                        onChange={this.handleEdit("slug", index)}
                    />
                </Grid>
            </>
        );
    }

    renderList() {
        const {list} = this.props;

        return list.map(this.renderLine, this);
    }

    render() {
        const {classes} = this.props;

        return (
            <Container>
                <Grid container spacing={2} className={classes.wrap}>
                    {this.renderList()}
                </Grid>

                <Button
                    name={"ADD"}
                    onClick={this.handleAdd}
                    variant="outlined"
                >
                    {"ADD"}
                </Button>
            </Container>
        );
    }
}

export default withStyles(theme => ({
    wrap: {
        padding: "15px 0"
    }
}))(Fields);
