import React, {Component} from 'react';
import {Tab, Tabs} from "@material-ui/core";
import SwipeableViews from "react-swipeable-views";
import FormProvider from "./context/FormProvider";
import ResultList from "./ResultList";
import FormList from "./FormList";
import PermissionList from "./PermissionList";

class Doppelganger extends Component {
    state = {
        active: 0,
    };

    /**
     *
     */
    handleChange = (event, active) => this.setState({active});

    /**
     *
     */
    handleChangeIndex = active => this.setState({active});

    render() {
        const
            {active} = this.state,
            {url} = this.props;

        return (
            <FormProvider url={url}>
                <div>
                    <Tabs
                        value={active}
                        onChange={this.handleChange}
                        indicatorColor="primary"
                        textColor="primary"
                    >
                        <Tab label="Результаты"/>
                        <Tab label="Формы"/>
                        <Tab label="Доступы"/>
                    </Tabs>

                    <SwipeableViews
                        axis={'x'}
                        index={active}
                        onChangeIndex={this.handleChangeIndex}
                    >
                        <ResultList/>

                        <FormList/>

                        <PermissionList/>
                    </SwipeableViews>
                </div>
            </FormProvider>
        );
    }
}

export default Doppelganger;
