import EditIcon from "@material-ui/icons/Edit";
import React from "react";

export default  [
    {
        title: "",
        render(row) {
            return (
                <EditIcon
                    style={{color: 'rgba(3, 155, 229,0.5)'}}
                    onClick={() => {
                        this.setState({
                            open: true,
                            edit: this.state.list[row]._id,
                        });
                    }}
                />
            );
        }
    },
    {
        title: "ID",
        render(row) {
            const {list} = this.state;

            return list[row]._id
        },
    },
    {
        title: "SLUG",
        render(row) {
            const {list} = this.state;

            return "SLUG"
        },
    },
    {
        title: "Timestamp",
        render(row) {
            const {list} = this.state;

            return list[row].timestamp
        },
    }
];
