import React, {Component} from 'react';
import ContentContext from "./ContentContext";
import {userContext} from "../../model/UserProvider";

class ContentProvider extends Component {
    static contextType = userContext;

    getData(entity) {
        const
            {url} = this.props,
            {token} = this.context;

        return filter => fetch(
            `${url}/${entity}/?${filter ?? ""}`,
            {
                headers: {
                    "authorization": token
                }
            }
        )
            .then(res => res.json())
            .catch(reason => {
                return [];
            });
    }

    getItem(entity) {
        const
            {url} = this.props,
            {token} = this.context;

        return id => fetch(
            `${url}/${entity}/${id}/`,
            {
                method: "GET",
                headers: {
                    "authorization": token
                }
            }
        ).then(res => res.json())
    }

    postItem(entity) {
        const
            {url} = this.props,
            {token} = this.context;

        return item => fetch(
            `${url}/${entity}/`,
            {
                method: "POST",
                body: JSON.stringify(item),
                headers: {
                    'Content-Type': 'application/json',
                    "authorization": token
                }
            }
        ).then(res => res.json())
    }

    putItem(entity) {
        const
            {url} = this.props,
            {token} = this.context;

        return (id, item) => fetch(
            `${url}/${entity}/${id}/`,
            {
                method: "PUT",
                body: JSON.stringify(item),
                headers: {
                    'Content-Type': 'application/json',
                    "authorization": token
                }
            }
        ).then(res => res.json())
    }

    deleteItem(entity) {
        const
            {url} = this.props,
            {token} = this.context;

        return id => fetch(
            `${url}/${entity}/${id}/`,
            {
                method: "DELETE",
                headers: {
                    "authorization": token
                }
            }
        ).then(res => res.json())
    }

    render() {
        const {children} = this.props;

        return (
            <ContentContext.Provider
                component={"div"}
                value={{
                    content: {
                        getList: this.getData("content"),
                        getItem: this.getItem("content"),
                        postItem: this.postItem("content"),
                        putItem: this.putItem("content"),
                        deleteItem: this.deleteItem("content"),
                    },
                    property: {
                        getList: this.getData("property"),
                        getItem: this.getItem("property"),
                        postItem: this.postItem("property"),
                        putItem: this.putItem("property"),
                        deleteItem: this.deleteItem("property"),
                    },
                    image: {
                        getList: this.getData("image"),
                        getItem: this.getItem("image"),
                        postItem: this.postItem("image"),
                        putItem: this.putItem("image"),
                        deleteItem: this.deleteItem("image"),
                    },
                    status: {
                        getList: this.getData("status"),
                        getItem: this.getItem("status"),
                        postItem: this.postItem("status"),
                        putItem: this.putItem("status"),
                        deleteItem: this.deleteItem("status"),
                    },
                }}
            >
                {children}
            </ContentContext.Provider>
        );
    }
}

export default ContentProvider;
