import React, {Component} from 'react';
import FormContext from "./FormContext";
import {userContext} from "../../model/UserProvider";

class FormProvider extends Component {
    static contextType = userContext;

    getData(entity, filter) {
        const
            {url} = this.props,
            {token} = this.context;

        return () => fetch(
            `${url}/${entity}/?${filter ?? ""}`,
            {
                headers: {
                    "authorization": token
                }
            }
        ).then(res => res.json())
    }

    getItem(entity) {
        const
            {url} = this.props,
            {token} = this.context;

        return id => fetch(
            `${url}/${entity}/${id}/`,
            {
                method: "GET",
                headers: {
                    "authorization": token
                }
            }
        ).then(res => res.json())
    }

    postItem(entity) {
        const
            {url} = this.props,
            {token} = this.context;

        return item => fetch(
            `${url}/${entity}/`,
            {
                method: "POST",
                body: JSON.stringify(item),
                headers: {
                    'Content-Type': 'application/json',
                    "authorization": token
                }
            }
        ).then(res => res.json())
    }

    putItem(entity) {
        const
            {url} = this.props,
            {token} = this.context;

        return (id, item) => fetch(
            `${url}/${entity}/${id}/`,
            {
                method: "PUT",
                body: JSON.stringify(item),
                headers: {
                    'Content-Type': 'application/json',
                    "authorization": token
                }
            }
        ).then(res => res.json())
    }

    deleteItem(entity) {
        const
            {url} = this.props,
            {token} = this.context;

        return id => fetch(
            `${url}/${entity}/${id}/`,
            {
                method: "DELETE",
                headers: {
                    "authorization": token
                }
            }
        ).then(res => res.json())
    }

    render() {
        const {children} = this.props;

        return (
            <FormContext.Provider
                value={{
                    form: {
                        getList: this.getData("form"),
                        getItem:  this.getItem("form"),
                        postItem: this.postItem("form"),
                        putItem: this.putItem("form"),
                        deleteItem: this.deleteItem("form"),
                    },
                    result: {
                        getList: this.getData("result"),
                        getItem:  this.getItem("result"),
                        postItem: this.postItem("result"),
                        putItem: this.putItem("result"),
                        deleteItem: this.deleteItem("result"),
                    },
                    permission: {
                        getList: this.getData("permission"),
                        getItem:  this.getItem("permission"),
                        postItem: this.postItem("permission"),
                        putItem: this.putItem("permission"),
                        deleteItem: this.deleteItem("permission"),
                    },
                }}
            >
                {children}
            </FormContext.Provider>
        );
    }
}

export default FormProvider;
