import React from 'react';
import PropTypes from 'prop-types';
import {TextField} from "@material-ui/core";
import {withStyles} from "@material-ui/core/styles";
import {context} from "../ContentList/context";


class Properties extends React.Component {
    static contextType = context;

    state = {
        properties: [],
    };
    
    componentDidMount() {
        const {fetchPropertyList} = this.context;
        
        fetchPropertyList().then(properties => {
            this.setState({properties})
        });
     }

    renderItem(item) {
        const
            {id, slug, property: {RU: {name}} = {}} = item,
            {classes, handleEdit, list} = this.props;

        return (
            <div key={slug}>
                <TextField
                    className={classes.margin}
                    label={name}
                    id={`property_${id}`}
                    value={list[slug]}
                    onChange={handleEdit(slug)}
                />
            </div>
        );
    }

    renderList() {
        const {properties} = this.state;

        return properties.map(this.renderItem, this);
    }

    render() {
        const {classes} = this.props;

        return (
            <form className={classes.root} noValidate autoComplete="off">
                {this.renderList()}
            </form>
        );
    }
}

Properties.propTypes = {};

export default withStyles(theme => ({
    margin: {
        width: "80%",
        margin: theme.spacing(3),
    },
}))(Properties);
